<template>
  <view class="container">
    <div class="card_container">
      <div class="card" :class="[item.cover?'cover':'']" v-for="item in cardList" :key="item.key" :style="item.style" @click="clickCard(item)">{{item.content.name}}</div>
    </div>
    <div class="tool">
      <button @click="removeThree">ÂèñÂá∫‰∏âÂº†Âç°Áâá</button>
      <button @click="random">ÈöèÊú∫</button>
      <button @click="again">ÂÜçÊù•‰∏ÄËΩÆ</button>
    </div>
    <div class="three_card_container">
      <div class="card">{{aaa}}</div>
    </div>
    <div class="choose_card_container">
      <div class="card" v-for="item in penddingList" :key="item.key" @click="clickCard(item)" :style="item.style">{{item.content.name}}</div>
      </div>
    </div>

  </view>
</template>

<script>
  
  class Card {
    // Âç°ÁâáÁöÑÂÆΩÈ´ò,Âè™ÊúâÂéüÊú¨È´òÂ∫¶ÁöÑ‰∏ÄÂçäÔºåÂõ†‰∏∫‰πãÂêé‰∏Ä‰∏™Âç°ÁâáÂç†2*2ÁöÑÊï∞ÁªÑÂ§ßÂ∞èÔºåÊñπ‰æøÂΩ¢Êàê‰∏äÂ±ÇÂç°ÁâáÂéãÂà∞‰∏ãÂ±ÇÂç°ÁâáÁöÑÊüê‰∏™ËßíÁöÑÊïàÊûú„ÄÇ
    static x = 20;
    static y = 25;
    static contentType=[];
    constructor({
      x,
      y,
      z,
      key
    }) {
      this.x = x;
      this.y = y;
      this.z = z;
      this.key = key;
      this.val = key;
      this.content = {};
      this.cover=false;
      this.style = `top: ${y*Card.y+30}px;left:${x*Card.x+30}px;`
    }
    setVal(val) {
      this.val = val-1;
      this.content = Card.contentType[val-1]
      this.style+=`${this.content.style}`
    }
    static setContentType(list){
      Card.contentType=list
    }
  }
  export default {
    data() {
      return {
        options: {},
        aaa: 'üßª',
        // ÊâÄÊúâÂç°Áâá
        cardList: [],
        // ‰øùÂ≠òÁöÑ‰∏âÂº†Âç°Áâá
        saveList: [],
        // ÈÄâÊã©ÁöÑÂç°Áâá
        penddingList: [],
        // Âà§Êñ≠ÈÅìÂÖ∑ÊòØÂê¶Â∑≤‰ΩøÁî®Ëøá
        tools: {
          three: true,
          random: true,
        },
        // Âà§Êñ≠ÊòØÂê¶‰ª•ÂèäËé∑ËÉú
        winner: false,
        // Êï∞ÁªÑÁöÑÂæàËΩ¥ÂíåÁ∫µËΩ¥
        xUnit: 0,
        yUnit: 0,
      };
    },
    methods: {
      // ÂàùÂßãÂåñÊ∏∏Êàè
      init(options) {
        // Ê∏ÖÁ©∫list
        this.cardList = []
        this.penddingList = []
        this.saveList = []
        // ÈáçÁΩÆtools
        this.tools.three = true
        this.tools.random = true
        // ÁªòÂà∂Âç°ÁâáÂú∞Âõæ
        this.getMaps(options)
      },
      // ÁªòÂà∂Âç°ÁâáÂú∞Âõæ
      getMaps(options) {
        // ÂàùÂßãÂåñÁΩëÁªúÂú∞Âõæ
        let cardMap = this.initMap(options)
        // Ê†πÊçÆÂç°ÁâáÂØÜÂ∫¶ÔºåÂêëcardmap‰∏≠ÊîæÂÖ•card,Ê≠§Êó∂ÁöÑÂç°ÁâáÊ≤°ÊúâÂÜÖÂÆπ
        cardMap = this.setCard(cardMap, options)
        // ËÆæÁΩÆÂç°ÁâáÁöÑÂÜÖÂÆπ
        this.setContent(options)
        // ËÆ°ÁÆóÂç°ÁâáÁöÑÈÅÆÁΩ©ÂÖ≥Á≥ª
        this.calcCover(cardMap)
      },
      clickCard(item){
        // Â∞ÜÁÇπÂáªÁöÑÂç°ÁâáÂÜ≤cardList‰∏≠ÂéªÈô§Ôºå‰øùÂ≠òÂú®penddingList‰∏≠
        let index=this.cardList.indexOf(item)
        this.cardList=this.cardList.slice(0,index).concat(this.cardList.slice(index+1))
        // ÈáçÊñ∞ËÆæÁΩÆitemÁöÑstyle
        item.style=`left:${(this.penddingList.length-1)*Card.x*2+60}px`
        this.penddingList.push(item)
        // ÈáçÊñ∞ËÆ°ÁÆóÈÅÆÊå°ÂÖ≥Á≥ª
        this.calcCover()
        //Âà§Êñ≠ÊòØÂê¶Êúâ‰∏â‰∏™ÈáçÂ§çÁöÑÂèØ‰ª•Ê∂àÈô§
        
      },
      // ËÆ°ÁÆóÂç°ÁâáÈÅÆÁΩ©ÂÖ≥Á≥ª
      calcCover(){
        // ‰ªéÂêéÂæÄÂâçÔºåÂêéÈù¢ÁöÑÂ±ÇÊï∞È´ò
        // ÂàùÂßãÂåñcoverÊï∞ÁªÑÔºåfalseË°®Á§∫Êîπ‰ΩçÁΩÆÊ≤°ÊúâÂç°ÁâáÔºåtrueË°®Á§∫ËØ•‰ΩçÁΩÆÁöÑ‰∏äÊñπÊúâÂç°ÁâáÔºåÈúÄË¶ÅËÆæÁΩÆcoverÊ†∑Âºè
        let coverMap=new Array(this.yUnit)
        for(let i=0;i<this.yUnit;i++){
          coverMap[i]=new Array(this.xUnit).fill(false)
        }
        for(let i=this.cardList.length-1;i>=0;i--){
          const item=this.cardList[i]
          const {x,y}=item
          if(coverMap[y][x]){
            item.cover=true
          }else if(coverMap[y+1][x]){
            item.cover=true
          }else if(coverMap[y][x+1]){
            item.cover=true
          }else if(coverMap[y+1][x+1]){
            item.cover=true
          }else{
            item.cover=false
          }
          coverMap[y][x]=true
          coverMap[y+1][x]=true
          coverMap[y][x+1]=true
          coverMap[y+1][x+1]=true
          
        }
      },
      // ËÆæÁΩÆÂç°ÁâáÁöÑÂÜÖÂÆπ
      setContent(options) {
        const { maxCard} = options
        const valStack = new Array(maxCard)
        // ÁªôÂç°ÁâáËÆæÁΩÆÂÄº
        this.cardList.forEach((item) => {
          // ÈöèÊú∫‰∏Ä‰∏™ÁßçÁ±ª
          const value = Math.ceil(Math.random() * maxCard);
          if (valStack[value]) {
            // Â∞ÜitemËµãÂÄºËøô‰∏™ÁßçÁ±ªÁöÑÂç°Áâá
            valStack[value].push(item);
            // Â∞Ü‰ªñ‰ª¨ËøõË°å‰∏â‰∏™‰∏â‰∏™ÈÖçÂØπ
            if (valStack[value].length === 3) {
              valStack[value].forEach((item) => {
                item.setVal(value);
              });
              valStack[value] = null;
            }
          } else {
            valStack[value] = [item];
          }
        });
        // ÂØπ‰∏∫ËøõË°åÂàÜÁ±ªÁöÑÈáçÊñ∞ÂàÜÁ±ª
        let count = 2;
        valStack.forEach(list => {
          list && list.map(item => {
            count++
            item.setVal(Math.floor(count / 3))
          })
        })
      },
      // ÂàùÂßãÂåñÂú∞ÂõæÔºåÁ°ÆËÆ§Âç°Áâá‰ΩçÁΩÆ
      setCard(map, options) {
        const {
          x,
          y,
          z,
          random
        } = options
        let key = 0
        const cardList = []
        const shrinkSpeed = 3
        for (let k = 0; k < z; k++) {
          const shrink = Math.floor((z - k - 1) / shrinkSpeed)
          const shrinkX = Math.min(Math.floor(this.xUnit / 2) - 2, shrink)
          const shrinkY = Math.min(Math.floor(this.yUnit / 2) - 2, shrink)
          // Ë°å
          // -1ÊòØÂõ†‰∏∫yÊòØ*2ÂêéÁöÑÔºåÊúÄÂêé‰∏ÄË°å‰∏çËÉΩÊîæÁΩÆÂç°ÁâáÔºå‰∏çÁÑ∂Â∞±‰ºöË∂ÖÂá∫ËæπÁïå
          for (let i = shrinkY; i < this.yUnit - 1 - shrinkY; i++) {
            // Âàó
            for (let j = shrinkX; j < Math.ceil((this.xUnit - 1) / 2); j++) {
              let canSetCard = true
              if (j > 0 && map[k][i][j - 1]) {
                // Â∑¶Ëæπ‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              } else if (i > 0 && map[k][i - 1][j]) {
                // ‰∏äÈù¢‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              } else if (i > 0 && j > 0 && map[k][i - 1][j - 1]) {
                // Â∑¶‰∏äËßí‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              } else if (i > 0 && map[k][i - 1][j + 1]) {
                // Âè≥‰∏äËßí‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              } else if (k > 0 && map[k - 1][i][j]) {
                // Ê≠£Â∫ï‰∏ã‰∏çËÉΩÊúâÂç°Áâá
                canSetCard = false
              }
              // Â¶ÇÊûúËøô‰∏™‰ΩçÁΩÆÂèØ‰ª•ÊîæÁΩÆÂç°ÁâáÔºåÈÇ£‰πàÊ†πÊçÆÈöèÊú∫Êï∞ÔºåËÆæÁΩÆÂç°ÁâáÁöÑÂØÜÂ∫¶
              if (canSetCard) {
                const rand = Math.random()
                if (rand < random) {
                  // ËÆæÁΩÆÂç°Áâá
                  const card = new Card({
                    x: j,
                    y: i,
                    z: k,
                    key
                  })
                  key++
                  map[k][i][j] = card
                  cardList.push(card)
                  // ÂØπÁß∞ÊîæÁΩÆÂç°Áâá
                  const mirror = this.xUnit - 2 - j
                  if (mirror > j) {
                    const item = new Card({
                      x: mirror,
                      y: i,
                      z: k,
                      key
                    })
                    map[k][i][mirror] = item
                    key++
                    cardList.push(item)
                  }
                }
              }
            }
          }
        }
        // Âç°ÁâáÂΩ¢Êàê‰∏âÁöÑÂÄçÊï∞,ÂéªÊéâÈ°∂Â±ÇÁöÑÂç°ÁâáÔºåÂõ†‰∏∫È°∂Â±ÇÁöÑÂç°ÁâáÊúÄÂ§ö
        cardList.reverse()
        for (let i = 0; i < cardList.length % 3; i++) {
          let item = cardList.pop()
          map[item.z][item.y][item.x] = 0
        }
        cardList.reverse()
        this.cardList = cardList
        return map
      },
      // ÂàùÂßãÂåñÁΩëÁªúÂú∞Âõæ
      initMap(options) {
        const {
          x,
          y,
          z
        } = options
        this.xUnit = x * 2
        this.yUnit = y * 2
        const cardMap = new Array(z)
        for (var i = 0; i < z; i++) {
          cardMap[i] = new Array(this.yUnit)
          for (var j = 0; j < this.yUnit; j++) {
            cardMap[i][j] = new Array(this.xUnit).fill(0)
          }
        }
        return cardMap
      },
      // Ê†πÊçÆmaxCardÂàùÂßãÂåñcardÁ±ª‰∏≠ÁöÑcontentTypeÊï∞ÁªÑÔºåÈöèÊú∫Áîü‰∫ßÂûÉÂúæ
      initContentType(){
        // 0:ÂèØÂõûÊî∂ÂûÉÂúæÔºõ1ÔºöÊúâÂÆ≥ÂûÉÂúæÔºõ2ÔºöÊπøÂûÉÂúæÔºõ3ÔºöÂπ≤ÂûÉÂúæ
        const contentList= [{
            name: 'üì¶',
            class: '0',
            style: 'background: #73b0ff'
          }, {
            name: 'üìö',
            class: '0',
            style: 'background: #73b0ff'
          }, {
            name: 'üî©',
            class: '0',
            style: 'background: #73b0ff'
          }, {
            name: 'üç∂',
            class: '0',
            style: 'background: #73b0ff'
          }, {
            name: 'üëó',
            class: '0',
            style: 'background: #73b0ff'
          }, {
            name: 'üíä',
            class: '1',
            style: 'background: #ff5c74'
          }, {
            name: 'üîã',
            class: '1',
            style: 'background: #ff5c74'
          }, {
            name: 'üß™',
            class: '1',
            style: 'background: #ff5c74'
          }, {
            name: 'üíâ',
            class: '1',
            style: 'background: #ff5c74'
          }, {
            name: 'üé®',
            class: '1',
            style: 'background: #ff5c74'
          }, {
            name: 'üö®',
            class: '1',
            style: 'background: #ff5c74'
          }, {
            name: 'üçé',
            class: '2',
            style: 'background: #82eb62'
          }, {
            name: 'üçó',
            class: '2',
            style: 'background: #82eb62'
          }, {
            name: 'üçå',
            class: '2',
            style: 'background: #82eb62'
          }, {
            name: 'üåø',
            class: '2',
            style: 'background: #82eb62'
          }, {
            name: 'üçÇ',
            class: '2',
            style: 'background: #82eb62'
          }, {
            name: 'üêü',
            class: '2',
            style: 'background: #82eb62'
          }, {
            name: 'üßª',
            class: '3',
            style: 'background: #ced5b2'
          }, {
            name: 'üö¨',
            class: '3',
            style: 'background: #ced5b2'
          }, {
            name: 'üëû',
            class: '3',
            style: 'background: #ced5b2'
          }, {
            name: 'üßØ',
            class: '3',
            style: 'background: #ced5b2'
          }]
          
        // ÈöèÊú∫Âç°ÁâáÊ†∑ÂºèÊï∞ÁªÑ
        // Ê¥óÁâåÁÆóÊ≥ï
        let shuffle=(arr)=>{
          for (let i = arr.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [arr[i], arr[j]] = [arr[j], arr[i]];
          }
          return arr;
        }
        const selected=shuffle(contentList).slice(0,this.options.maxCard)
         Card.setContentType(selected)
      },
      // ÂÜçÊù•‰∏ÄËΩÆ
      again() {
        this.init()
      },
      // ÈöèÊú∫
      random() {

      },
      // ÂèñÂá∫‰∏âÂº†Âç°Áâá
      removeThree() {

      }
    },
    onLoad(option) {
        this.options=JSON.parse(option.options)      
        // Ê†πÊçÆmaxCardÂàùÂßãÂåñcardÁ±ª‰∏≠ÁöÑcontentTypeÊï∞ÁªÑÔºåÈöèÊú∫Áîü‰∫ßÂûÉÂúæ
        this.initContentType()
        // ÂàùÂßãÂåñÊ∏∏Êàè
        this.init(this.options)
    },
    
  }
</script>

<style lang="scss">
  .container {
    width: 100vw;
    height: 100vh;
    position: relative;

    .card_container {
      position: relative;
      padding: 30px;
      height: 300px;
    }

    .tool {
      // position: absolute;
      display: flex;
      width: 100vw;
      // transform: translate(-50%,0);
      justify-content: space-around;
    }

    .three_card_container {
      position: relative;
      display: flex;
      width: 100%;
      height: 55px;
      padding: 10px 20px;
      margin-top: 10px;

      // background-color: green;
      .card {
        margin-right: 7px;
      }
    }

    .choose_card_container {
      position: relative;
      display: flex;
      margin-top: 20px;
      width: 95%;
      height: 78px;
      padding: 10px 15px;
      background-color: #82eb62;
      border: 1px solid black;
      border-radius: 20px;
      box-sizing: border-box;
      margin: 10px 10px;

      .card {
        margin-right: 7px;
      }
    }
  }

  .card {
    font-size: 28px;
    text-align: center;
    position: absolute;
    border-radius: 2px;
    box-sizing: border-box;
    background: #ddd;
    opacity: 1;
    cursor: pointer;
    transition: all 0.3s;
    box-shadow: 0px 3px 0 0 #fff, 0 8px 0 0 #ddd, 0 8px 0 2px #333, 0 0 0 2px #333;

    &:hover {
      transform: scale3d(1.1, 1.1, 1.1);
      z-index: 1;
    }

    // width: 40px;
    // height: 50px;
    // // background-color: pink;
    // border: 1px solid black;
    // border-radius: 5px;

    // .content{
    //   padding: 2px;
    //   height: 40px;
    //   // background-color: #73d3ff;
    //   background-color: white;
    //   font-size: 25px;
    //   text-align: center;
    //   line-height: 40px;
    //   border-radius: 5px;
    //   // pointer-events: none;
    //     // box-shadow: 0px 3px 0 0 #999, 0 8px 0 0 #666, 0 8px 0 2px #000, 0 0 0 2px #000;
    //     box-shadow: 0px 3px 0 0 #fff, 0 8px 0 0 #ddd, 0 8px 0 2px #333, 0 0 0 2px #333;
    // }
  }

  .cover {
    pointer-events: none;
    box-shadow: 0px 3px 0 0 #999, 0 8px 0 0 #666, 0 8px 0 2px #000, 0 0 0 2px #000;
  }

  .cover:after {
    border-radius: 2px;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #000;
    opacity: 0.55;
  }
</style>
